.banner{

    color: white;
    height: 448px;
    padding-top: 15%;
}

.banner_contents{
  
    height: 100%;
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: .3em;
}

.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: .8rem;
    max-width: 360px;
    height: 80px;
}

.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: .2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: .5rem;
    margin-right: 1rem;
    padding-bottom: .5rem;
    background-color: rgba(51, 51, 51, 0.5);


}

.banner_button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all .2s;
}

.banner-fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #181818
    );
}
