.row{
    margin-left: 10px;
    color: white;
}
.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
   
    padding: 20px;
    
}

img.row_posterLarge{
    max-height: 250px;
    object-fit: contain;
    transition: transform .450s;
    margin-right: 10px;  
}

img.row_posterLarge:hover{
    transform: scale(1.1);
    opacity: 1;

}

.row_posters::-webkit-scrollbar-track {
    display: none;
}

.row_posters::-webkit-scrollbar{
    display: none;
}

.row_poster{
    
    object-fit: contain;
    max-height: 100px;
    transition: transform .450s;
    margin-right: 10px;

}

.row_poster:hover{
    transform: scale(1.1);
    opacity: 1;
}



